import * as React from 'react'
import { NextContext } from 'next'
import Layout from '../components/Layout'
import { ISeo, IPost } from '../interfaces';
import { findOnePost, getImage } from '../utils/flameLinkUtils';
// import ReactHighMark from 'react-mark-highlight'
import './post.scss'


import dynamic from 'next/dynamic';

const ReactHighMark = dynamic(
  () => import('react-mark-highlight'),
  {
    ssr: false
  }
);

type RequestQuery = {
  slug: string,
}

type Props = {
  item: IPost,
  errors?: string
}

class BlogPostPage extends React.Component<Props> {
  static getInitialProps = async ({ query }: NextContext<RequestQuery>) => {

    try {
      const item = await findOnePost(query.slug);
      return { item }
    } catch (err) {
      return { errors: err.message }
    }
  }


  render() {
    const { item, errors } = this.props;

    if (errors) {
      return (
        <Layout>
          <p><span style={{ color: 'red' }}>Error:</span> {errors}</p>
        </Layout>
      )
    }

    const seoMeta: ISeo = {
      title: item.title,
      description: item.summary,
      twitterDescription: item.twitterSummary,
      image: ((item.featuredImage) ? getImage(item.imageName) : 'https://avatars1.githubusercontent.com/u/28115284?s=460&v=4'),
      keywords: item.tags,
      type: 'article',
      url: `sanyamarya.io/post?slug=${item.slug}`,
      article: {
        published_on: item.date,
        section: item.category
      }
    }
    console.log(seoMeta);

    return (
      <Layout seo={seoMeta}>
        <div className="postContainer">
          <div className="header">
            {item.featuredImage &&
              <img src={getImage(item.imageName)}
                alt={item.slug} className="featuredImage" />}

          </div>

          <div className="bodyArea">
            <div className="content">
              <ReactHighMark source={item.content} style="diablo" />

              <div className="info">
                <span className="publishedDate">{item.date}</span>
              </div>
            </div>
            <div className="allOther">
              <h1>{item.title}</h1>

              <script src="https://cdn.lightwidget.com/widgets/lightwidget.js"></script><iframe src="//lightwidget.com/widgets/a5dad4997cb95544b7f277042135241f.html" scrolling="no" allowTransparency={true} className="lightwidget-widget" style={{width:'100%',border:0,overflow:'hidden'}}></iframe>

              <iframe src="https://snapwidget.com/embed/680088" className="snapwidget-widget" allowTransparency={true} frameBorder="0" scrolling="no" style={{border:"none", overflow:"hidden", width:"100%"}}></iframe>
            </div>
          </div>
        </div>

      </Layout>
    )
  }
}

export default BlogPostPage
